.follow-us__heading {
  display: block;
  width: 200px;
  margin-left: calc(50% - 98px);
  margin-bottom: 19px;
  padding-top: 0;
  font-family: $font-headings;
  font-weight: 700;
  font-size: 34px;
  line-height: 44px;

  span {
    font-size: 42px;
  }
}

.follow-us__follow-link {
  display: inline-block;
  vertical-align: middle;
  width: 74px;
  margin-left: calc(50% - 37px);
  padding-bottom: 4px;
  margin-bottom: 44px;
  font-family: $font;
  font-weight: 500;
  font-size: 16px;
  line-height: 21px;
  border-bottom: 1px solid #a87b62;

  &:hover {
    color: $gray-font;
  }

  &:focus {
    outline: none;
    box-shadow: 0 0 2px 1px $brown;
  }
}

.follow-us__images-list {
  display: flex;
  flex-direction: row;
}

.follow-us__column--first {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  vertical-align: top;
  width: 57.5%;
}

.follow-us__column--second {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  vertical-align: top;
  width: 42.5%;
}

.follow-us__images-wrap {
  display: flex;
  width: 100%;
  height: 100%;
}

.follow-us__image {
  display: block;
  width: 100%;
  height: auto;
}

@media (min-width: $desktop-width) {
  .follow-us__heading {
    margin-top: 129px;
  }

  .follow-us__wrapper {
    margin-left: 6.07%;
    margin-right: 6.07%;
    margin-bottom: 114px;
  }

  .follow-us__column--first {
    .follow-us__images-item {
      display: flex;
      justify-content: flex-start;
      margin: 2.17%;
      align-self: start;
    }

    .follow-us__images-item:first-of-type {
      width: 36.4%;
    }

    .follow-us__images-item:nth-of-type(2) {
      width: 63.5%;
    }

    .follow-us__images-item:nth-of-type(3) {
      position: absolute;
      left: 0;
      bottom: 0;
      width: calc(37.68% - 30px);
    }
  }

  .follow-us__column--second {
    .follow-us__images-item {
      display: flex;
      justify-content: flex-start;
      margin: 3%;
      align-self: start;
    }

    .follow-us__images-item:first-of-type {
      align-self: start;
      width: 45.5%;
    }

    .follow-us__images-item:nth-of-type(2) {
      align-self: start;
      margin-bottom: 0;
      width: 42.5%;
    }

    .follow-us__images-item:nth-of-type(3) {
      align-self: start;
      margin-top: 1%;
      width: 42.5%;
    }

    .follow-us__images-item:nth-of-type(4) {
      align-self: start;
      margin-top: 6%;
      width: 45.5%;
    }
  }
}

@media (min-width: $min-tablet-width) and (max-width: $max-tablet-width) {
  .follow-us__heading {
    margin-top: 72px;
  }

  .follow-us__follow-link {
    margin-bottom: 50px;
  }

  .follow-us__wrapper {
    margin-left: 4.7%;
    margin-right: 4.7%;
    margin-bottom: 70px;
  }

  .follow-us__column--first {
    .follow-us__images-item {
      display: flex;
      justify-content: flex-start;
      margin: 2.17%;
      align-self: start;
    }

    .follow-us__images-item:first-of-type {
      width: 36.4%;
    }

    .follow-us__images-item:nth-of-type(2) {
      width: 63.5%;
    }

    .follow-us__images-item:nth-of-type(3) {
      position: absolute;
      left: 0;
      bottom: 0;
      width: calc(37.68% - 18px);
    }
  }

  .follow-us__column--second {
    .follow-us__images-item {
      display: flex;
      justify-content: flex-start;
      margin: 3%;
      align-self: start;
    }

    .follow-us__images-item:first-of-type {
      align-self: start;
      width: 45.5%;
    }

    .follow-us__images-item:nth-of-type(2) {
      align-self: start;
      margin-bottom: 0;
      width: 42.5%;
    }

    .follow-us__images-item:nth-of-type(3) {
      align-self: start;
      margin-top: 1%;
      width: 42.5%;
    }

    .follow-us__images-item:nth-of-type(4) {
      align-self: start;
      margin-top: 6%;
      width: 45.5%;
    }
  }
}

@media (min-width: $min-mobile-width) and (max-width: $max-mobile-width) {
  .follow-us__heading {
    margin-top: 55px;
  }

  .follow-us__follow-link {
    margin-bottom: 39px;
  }

  .follow-us__wrapper {
    margin-right: 2.34%;
    margin-left: 2.34%;
    margin-bottom: 62px;
  }

  .follow-us__images-list {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
  }

  .follow-us__column--first {
    width: 100%;

    .follow-us__images-item {
      display: flex;
      justify-content: space-between;
      margin: 2.6%;
      align-self: start;
    }

    .follow-us__images-item:first-of-type {
      width: 42%;
    }

    .follow-us__images-item:nth-of-type(2) {
      width: 47.6%;
      margin-bottom: 2%;
    }

    .follow-us__images-item:nth-of-type(3) {
      position: absolute;
      left: 0;
      bottom: -6px;
      width: 42%;
    }
  }

  .follow-us__column--second {
    flex-direction: row-reverse;
    width: 100%;

    .follow-us__images-item {
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      vertical-align: bottom;
      margin: 2.45%;
      align-self: start;
    }

    .follow-us__images-item:first-of-type {
      align-self: flex-end;
      width: 48%;
    }

    .follow-us__images-item:nth-of-type(3) {
      align-self: flex-end;
      width: 42%;
    }

    .follow-us__images-item:nth-of-type(2n) {
      display: none;
    }
  }
}
