/* poppins-regular - latin */
@font-face {
  font-family: "poppins";
  font-style: normal;
  font-weight: 400;
  src: local("poppins Regular"),
    local("poppins-Regular"),
    url("../fonts/poppins/poppins-v9-latin-regular.woff2") format("woff2"),
    url("../fonts/poppins/poppins-v9-latin-regular.woff") format("woff");
  font-display: swap;
}
